var _this;
export default {
	data() {
		return {
			NoticeList: [],
			LoadSta: 0, //0默认状态），1加载中），2没有了）
			page: 1,
			limit: 10,
		}
	},
	mounted() {
		_this = this;
		_this.getnewsList();
	},
	methods: {
		//加载更多
		getnewsList() {
			if (_this.LoadSta == 2) {
				uni.showToast({
					title: '没有更多数据了',
					icon: 'none'
				});
				return;
			}
			_this.LoadSta = 1;
			var allParams = {
				url: 'queryAllNoticeInfoByLimit',
				setUpUrl: true,
				data: {
					page: _this.page,
					limit: _this.limit
				},
				type: 'get',
				sCallback: function(res) {
					if (res.code == 1) {
						_this.NoticeList = [];
						_this.LoadSta = 2;
						uni.showToast({
							title: '获取公告数据失败',
							icon: 'none'
						})
					} else if (res.code == 0) {
						console.log("res.data.length == 0 ： ", res.data.length == 0);
						if (res.data.length == 0) {
							_this.LoadSta = 2;
							//_this.NoticeList = [];
						} else {
							_this.page++;
							_this.LoadSta = 0;
							_this.NoticeList = _this.NoticeList.concat(res.data);
						}
						console.log("_this.LoadSta ： ", _this.LoadSta);

					}
				}
			};
			_this.$base.request(allParams, _this);
		}
	}
}
